body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --dark-mode-background-color:  rgb(40,44,51);
  --dark-mode-text-color: whitesmoke;
  --light-mode-background-color: whitesmoke;
  --light-mode-text-color: rgb(40,44,51);
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.App {
    display: flex;
    align-items: center;
    height: 100vh;
}

#main {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
    width: 100vw;
    height: 100vh;
    background-color: rgb(40,44,51);
    background-color: var(--dark-mode-background-color);
    color: whitesmoke;
    color: var(--dark-mode-text-color);
}


@media screen and (max-width: 992px) {
    .App {
        height: 100vh;
    }
    #main {
        width: 100vw;
    }
    .header {
        font-size: 1rem;
        padding: 35px !important;
        width: 90vw !important;
        height: 1rem;
    }
    .header::after {
        width: 70vw !important;
    }
    .header 
    h1 {
        font-size: 1rem;
        text-align: left;
    }
    .header 
    h2 {
        font-size: 2rem;
        text-align: left;
        font-weight: 100;
    }
    .header 
    .header-right {
        text-align: right;
        width: 100%;
    }

    .header 
    .header-right 
    img {
        width: 80px !important;
        height: 80px !important;
        top: -80px;
    }
    .header 
    .header-left {
        width: 100%;
        
    }

}

.header {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 15px;
    position: relative;
    margin-bottom: 20px;
    width: 35vw;
}

.header::after {
    content: "";
    margin: 0 auto;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 80%;
    border-bottom: 0.5px solid whitesmoke;
    border-bottom: 0.5px solid var(--dark-mode-text-color);
}


.header 
.header-left,
.header 
.header-right {
    display: flex;
    flex-direction: column;
}

.header 
.header-left {
    align-items: flex-start;
    text-align: left;
}

.header 
.header-left 
.date {
    text-align: left;
    margin: 10px 0;
}

.header 
.header-right {
    align-items: flex-end;
    position: relative;
}

.header 
.header-right 
img {
    width: 80px;
    height: 80px;
    position: absolute;
    top: -70px;
    right: -10px;
}

.sun-position {
    display: flex;
    align-items: center;
    font-size: 0.8rem;
}

.sunrise, 
.sunset {
    position: relative;
}



